import { DATE_PARTS } from '../utils/schedule';
import * as H from '../utils/dateTime';
export function calcStepFromConstraints(constraints) {
  // @ts-ignore
  var part = DATE_PARTS.find(function (part) {
    return Boolean(constraints[part]) && Boolean(constraints[part].step);
  }); // @ts-ignore

  return part && constraints[part].step * H[part];
}